export const serviceSchema = (serviceTitle: string, serviceDescription: string, areaName: string = 'Newton-le-Willows') => ({
  '@context': 'https://schema.org',
  '@type': 'Service',
  '@id': `https://rrm-cleaning.co.uk/services/${serviceTitle.toLowerCase().replace(/\s+/g, '-')}`,
  name: serviceTitle,
  description: serviceDescription,
  provider: {
    '@type': 'LocalBusiness',
    '@id': 'https://rrm-cleaning.co.uk/#organization',
    name: 'R.R.M External Cleaning Specialist',
  },
  areaServed: {
    '@type': 'City',
    name: areaName,
  },
  hasOfferCatalog: {
    '@type': 'OfferCatalog',
    name: `${serviceTitle} Services`,
    itemListElement: {
      '@type': 'Offer',
      itemOffered: {
        '@type': 'Service',
        name: serviceTitle,
      },
    },
  },
});

export const breadcrumbSchema = (items: { name: string; url: string }[]) => ({
  '@context': 'https://schema.org',
  '@type': 'BreadcrumbList',
  itemListElement: items.map((item, index) => ({
    '@type': 'ListItem',
    position: index + 1,
    name: item.name,
    item: item.url,
  })),
});

export const faqSchema = (faqs: { question: string; answer: string }[]) => ({
  '@context': 'https://schema.org',
  '@type': 'FAQPage',
  mainEntity: faqs.map((faq) => ({
    '@type': 'Question',
    name: faq.question,
    acceptedAnswer: {
      '@type': 'Answer',
      text: faq.answer,
    },
  })),
});

